* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  border: 2px solid #90ee90;
  border-radius: 6px;
  margin-bottom: 16px;
  padding: 8px;
}

#search-box:focus {
  border: 2px solid #90ee90;
}

.country-list {
  margin: 0;
}

.country__item {
  width: fit-content;
  border: 1px solid #add8e6;
  border-radius: 4px;
  align-items: baseline;
  gap: 10px;
  margin-bottom: 10px;
  padding: 10px;
  list-style: none;
  display: flex;
}

.country__img {
  width: 70px;
  height: 40px;
  align-self: center;
}

.country-wrapper {
  gap: 20px;
  display: flex;
}

.country__item--info {
  width: fit-content;
  border: 3px solid #add8e6;
  border-radius: 10px;
  flex-direction: column;
  gap: 5px;
  padding: 10px;
  display: flex;
}

.country__item--accent {
  font-size: 18px;
  font-weight: 700;
}

.country__text {
  margin: 0;
  padding-left: 10px;
  font-size: 16px;
  font-weight: 400;
}

.label {
  font-family: Courier New, Courier, monospace;
  font-size: 21px;
  font-style: italic;
}

